html {
    box-sizing: border-box;
    scroll-behavior: smooth;

    background-color: #fff;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
    color: inherit;
}

ul,
li {
    list-style: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;

}